import React from "react";

interface User {
  id: string;
  name: string;
  email: string;
  phone: string;
  role: string;
  dateJoined: string;
  downloads: number;
  lastDownload: string;
  status: string;
}

export default function UserRow({ user }: { user: User }) {
  return (
    <tr className="border-b hover:bg-gray-50">
      <td className="py-2 font-medium">{user.name}</td>
      <td className="py-2">{user.email}</td>
      <td className="py-2">{user.phone}</td>
      <td className="py-2">
        <span className={`px-2 py-1 rounded text-xs font-semibold ${user.role === "Admin" ? "bg-green-100 text-green-700" : "bg-pink-100 text-pink-700"}`}>{user.role}</span>
      </td>
      <td className="py-2">{user.dateJoined}</td>
      <td className="py-2 text-center">{user.downloads}</td>
      <td className="py-2">{user.lastDownload}</td>
      <td className="py-2">
        <span className={`px-2 py-1 rounded text-xs font-semibold ${user.status === "Active" ? "bg-green-100 text-green-700" : "bg-gray-200 text-gray-700"}`}>{user.status}</span>
      </td>
      <td className="py-2 text-center">
        <button className="text-red-500 hover:text-red-700" title="Delete User">
          üóëÔ∏è
        </button>
      </td>
    </tr>
  );
} 